// components/enrollment-banner.tsx
'use client';

import { useState, useEffect } from 'react';
import { X } from 'lucide-react';

interface IntakePeriod {
  month: number; // 1-12 (January = 1, February = 2, etc.)
  name: string;
  year: number;
}

const EnrollmentBanner = () => {
  const [isVisible, setIsVisible] = useState(false);
  const [currentIntake, setCurrentIntake] = useState<IntakePeriod | null>(null);

  // Function to get the current active intake period
  const getCurrentIntake = (): IntakePeriod | null => {
    const now = new Date();
    const currentMonth = now.getMonth() + 1; // getMonth() returns 0-11, we need 1-12
    const currentYear = now.getFullYear();

    // Define intake periods: January (1), May (5), September (9)
    const intakePeriods = [
      { month: 1, name: 'January' },
      { month: 5, name: 'May' },
      { month: 9, name: 'September' }
    ];

    // Check each intake period to see if we're in the 2-month window before it
    for (const intake of intakePeriods) {
      let intakeYear = currentYear;
      let showStartMonth = intake.month - 2;
      let showEndMonth = intake.month;

      // Handle year boundary crossing
      if (showStartMonth <= 0) {
        showStartMonth += 12;
        intakeYear = currentYear - 1;
      }

      // Check if current date falls within the banner display window
      const isInDisplayWindow = 
        (currentMonth >= showStartMonth && currentMonth < showEndMonth && 
         (showStartMonth > showEndMonth ? currentYear >= intakeYear : currentYear === intakeYear)) ||
        (showStartMonth > showEndMonth && currentMonth >= showStartMonth && currentYear === intakeYear) ||
        (showStartMonth > showEndMonth && currentMonth < showEndMonth && currentYear === intakeYear + 1);

      if (isInDisplayWindow) {
        // Determine the correct year for the intake
        const finalIntakeYear = intake.month <= currentMonth ? currentYear + 1 : currentYear;
        
        return {
          month: intake.month,
          name: intake.name,
          year: finalIntakeYear
        };
      }
    }

    return null;
  };

  useEffect(() => {
    const intake = getCurrentIntake();
    setCurrentIntake(intake);
    setIsVisible(intake !== null);
  }, []);

  const handleClose = () => {
    setIsVisible(false);
  };

  const handleJoinNow = () => {
    // You can customize this to redirect to your enrollment page
    window.open('/enrollment', '_blank');
    // Or scroll to a specific section
    // document.getElementById('enrollment-section')?.scrollIntoView({ behavior: 'smooth' });
  };

  if (!isVisible || !currentIntake) {
    return null;
  }

  return (
    <>
      {/* Overlay */}
      <div className="fixed inset-0 bg-black bg-opacity-50 z-40 flex items-center justify-center p-4">
        {/* Banner Modal */}
        <div className="bg-white rounded-lg shadow-2xl max-w-md w-full mx-4 relative transform transition-all duration-300 ease-out">
          {/* Close Button */}
          <button
            onClick={handleClose}
            className="absolute top-3 right-3 p-2 hover:bg-gray-100 rounded-full transition-colors duration-200 z-10"
            aria-label="Close banner"
          >
            <X className="w-5 h-5 text-gray-500 hover:text-gray-700" />
          </button>

          {/* Banner Content */}
          <div className="p-8 text-center">
            {/* Header */}
            <div className="mb-6">
              <h2 className="text-2xl font-bold text-gray-900 mb-2">
                {currentIntake.name} {currentIntake.year} Intake
              </h2>
              <div className="inline-block bg-green-100 text-green-800 px-3 py-1 rounded-full text-sm font-medium">
                Ongoing
              </div>
            </div>

            {/* Message */}
            <div className="mb-8">
              <p className="text-gray-600 leading-relaxed">
                {currentIntake.name} {currentIntake.year} intake is now open
                for <strong>all our courses</strong>. Submit your
                application by clicking the
                button below
              </p>
            </div>

            {/* CTA Button */}
            <button
              onClick={handleJoinNow}
              className="bg-red-500 hover:bg-red-600 text-white font-bold py-3 px-8 rounded-lg transition-all duration-200 transform hover:scale-105 hover:shadow-lg uppercase tracking-wide text-sm"
            >
              JOIN NOW
            </button>
          </div>

          {/* Decorative Elements */}
          <div className="absolute top-0 left-0 w-full h-1 bg-gradient-to-r from-red-500 via-orange-500 to-yellow-500"></div>
        </div>
      </div>
    </>
  );
};

export default EnrollmentBanner;