// src/lib/queryClient.ts
import { QueryClient, QueryFunction } from "@tanstack/react-query";

// Error handling utility
async function throwIfResNotOk(res: Response) {
  if (!res.ok) {
    const text = (await res.text()) || res.statusText;
    throw new Error(`${res.status}: ${text}`);
  }
}

// Generic API request function
export async function apiRequest(
  method: string,
  url: string,
  data?: unknown | undefined,
): Promise<Response> {
  const res = await fetch(url, {
    method,
    headers: data ? { "Content-Type": "application/json" } : {},
    body: data ? JSON.stringify(data) : undefined,
    credentials: "include",
  });

  await throwIfResNotOk(res);
  return res;
}

// Types for better TypeScript support
type UnauthorizedBehavior = "returnNull" | "throw";

// Enhanced query function with proper error handling
export const getQueryFn: <T>(options: {
  on401: UnauthorizedBehavior;
}) => QueryFunction<T> =
  ({ on401: unauthorizedBehavior }) =>
  async ({ queryKey, signal }) => {
    const url = queryKey.join("/") as string;
    
    const res = await fetch(url, {
      credentials: "include",
      signal, // Support for AbortController for better cancellation
    });

    // Handle unauthorized access based on configuration
    if (unauthorizedBehavior === "returnNull" && res.status === 401) {
      return null;
    }

    await throwIfResNotOk(res);
    return await res.json();
  };

// Updated QueryClient with your original settings but modern optimizations
export const queryClient = new QueryClient({
  defaultOptions: {
    queries: {
      queryFn: getQueryFn({ on401: "throw" }),
      
      // Your original conservative settings
      refetchInterval: false,
      refetchOnWindowFocus: false,
      staleTime: Infinity,
      retry: false,
      
      // Modern Next.js optimizations - Updated property name
      gcTime: 1000 * 60 * 60 * 24, // 24 hours cache (renamed from cacheTime)
      refetchOnReconnect: true, // Good for network reconnection
      
      // Better error handling
      throwOnError: true,
    },
    mutations: {
      retry: false, // Keep your original setting
      
      // Add network error handling
      networkMode: 'online',
    },
  },
});